import React from 'react';

const Actividades = () => {
  return (
    <div class="panel panel-inverse" data-sortable-id="form-stuff-1" style="">
      <div class="panel-heading ui-sortable-handle">
        <h4 class="panel-title">Form Controls</h4>
        <div class="panel-heading-btn">
          <a href="javascript:;" class="btn btn-xs btn-icon btn-circle btn-default" data-click="panel-expand">
            <i class="fa fa-expand"></i>
          </a>
          <a href="javascript:;" class="btn btn-xs btn-icon btn-circle btn-success" data-click="panel-reload">
            <i class="fa fa-redo"></i>
          </a>
          <a href="javascript:;" class="btn btn-xs btn-icon btn-circle btn-warning" data-click="panel-collapse">
            <i class="fa fa-minus"></i>
          </a>
          <a href="javascript:;" class="btn btn-xs btn-icon btn-circle btn-danger" data-click="panel-remove">
            <i class="fa fa-times"></i>
          </a>
        </div>
      </div>

      <div class="panel-body">
        <div class="alert alert-muted">
          Textual form controlslike <code>&lt;input&gt;</code>s, <code>&lt;select&gt;</code>s, and{' '}
          <code>&lt;textarea&gt;</code>sare styled with the .form-control class. Included are styles for general
          appearance, focus state, sizing, and more.
        </div>
        <form>
          <div class="form-group row m-b-15">
            <label class="col-form-label col-md-3">Email address</label>
            <div class="col-md-9">
              <input type="email" class="form-control m-b-5" placeholder="Enter email" />
              <small class="f-s-12 text-grey-darker">We'll never share your email with anyone else.</small>
            </div>
          </div>
          <div class="form-group row m-b-15">
            <label class="col-form-label col-md-3">Example select</label>
            <div class="col-md-9">
              <select class="form-control">
                <option>1</option>
                <option>2</option>
                <option>3</option>
                <option>4</option>
                <option>5</option>
              </select>
            </div>
          </div>
          <div class="form-group row m-b-15">
            <label class="col-form-label col-md-3">Example multiple select</label>
            <div class="col-md-9">
              <select multiple="" class="form-control">
                <option>1</option>
                <option>2</option>
                <option>3</option>
                <option>4</option>
                <option>5</option>
              </select>
            </div>
          </div>
          <div class="form-group row m-b-15">
            <label class="col-form-label col-md-3">Example textarea</label>
            <div class="col-md-9">
              <textarea class="form-control" rows="3"></textarea>
            </div>
          </div>
        </form>
      </div>

      <div class="hljs-wrapper">
        <pre>
          <code class="html hljs xml">
            <span class="hljs-comment">&lt;!-- email --&gt;</span>
            <span class="hljs-tag">
              &lt;<span class="hljs-name">input</span> <span class="hljs-attr">type</span>=
              <span class="hljs-string">"email"</span> <span class="hljs-attr">class</span>=
              <span class="hljs-string">"form-control"</span> <span class="hljs-attr">placeholder</span>=
              <span class="hljs-string">"Enter email"</span> /&gt;
            </span>
            <span class="hljs-comment">&lt;!-- select --&gt;</span>
            <span class="hljs-tag">
              &lt;<span class="hljs-name">select</span> <span class="hljs-attr">class</span>=
              <span class="hljs-string">"form-control"</span>&gt;
            </span>
            ...
            <span class="hljs-tag">
              &lt;/<span class="hljs-name">select</span>&gt;
            </span>
            <span class="hljs-comment">&lt;!-- multiple select --&gt;</span>
            <span class="hljs-tag">
              &lt;<span class="hljs-name">select</span> <span class="hljs-attr">multiple</span>{' '}
              <span class="hljs-attr">class</span>=<span class="hljs-string">"form-control"</span>&gt;
            </span>
            ...
            <span class="hljs-tag">
              &lt;/<span class="hljs-name">select</span>&gt;
            </span>
            <span class="hljs-comment">&lt;!-- textarea --&gt;</span>
            <span class="hljs-tag">
              &lt;<span class="hljs-name">textarea</span> <span class="hljs-attr">class</span>=
              <span class="hljs-string">"form-control"</span> <span class="hljs-attr">rows</span>=
              <span class="hljs-string">"3"</span>&gt;
            </span>
            <span class="hljs-tag">
              &lt;/<span class="hljs-name">textarea</span>&gt;
            </span>
          </code>
        </pre>
      </div>
    </div>
  );
};

export default Actividades;
